.uni-modal__wrap{align-items:center;display:flex;justify-content:center;left:50%;position:fixed;top:50%;transform:translate(-50%,-50%);z-index:1000}.uni-modal__wrap[data-mask=true]{background-color:#00000073;height:100%;width:100%}.uni-modal{border:1px solid #ccc;border-radius:2px;box-shadow:0 3px 6px -4px #0000001f,0 6px 16px #00000014,0 9px 28px 8px #0000000d;flex-direction:column;max-height:80vh;max-width:80vw;overflow:hidden;position:relative}.uni-modal,.uni-modal__close-button{background-color:#fff;box-sizing:border-box;display:flex}.uni-modal__close-button{text-rendering:unset;align-items:center;appearance:unset;border:unset;border-image:unset;color:unset;cursor:pointer;justify-content:center;padding:19px;position:absolute;right:0;top:0;transition:background-color .4s;-ms-writing-mode:unset;writing-mode:unset}.uni-modal__close-button:hover{background-color:rgba(0,0,0,.06)}.uni-modal__header{background:#fff;border-bottom:1px solid rgba(0,0,0,.06);border-radius:2px 2px 0 0;box-sizing:border-box;color:#000000d9;flex:0 0 auto;font-weight:700;padding:16px 24px}.uni-modal__body{box-sizing:border-box;flex:1 1 0%;overflow:auto;padding:24px}.uni-modal__footer{border-top:1px solid rgba(0,0,0,.06);box-sizing:border-box;flex:0 0 auto;padding:10px 16px;text-align:right}.uni-modal-button{background:#fff;border:1px solid #d9d9d9;border-radius:2px;box-shadow:0 2px #00000004;color:#000000d9;cursor:pointer;display:inline-block;height:32px;margin-right:10px;padding:4px 15px;text-align:center;transition:color .4s,background .4s,border-color .4s}.uni-modal-button:last-of-type{margin-right:0}.uni-modal-button:hover{color:#40a9ff}.uni-modal-button.uni-modal-button--primary{background:#1890ff;border-color:#1890ff;color:#fff}.uni-modal-button.uni-modal-button--primary:hover{background:#40a9ff;border-color:#40a9ff}